import infoBox from "../../components/boxstyle/infobox.vue"
import btn from "../../components/boxstyle/buttonstyle.vue"
import messBox from '../../components/message.vue'
import uploadMode from '../../components/uploadMode.vue'

	export default {
		data() {
			return {
				// 留言模块接收值
				message: '',
				message2: '',
				// 上传图片模块
				imgPathList: [],
				// 获取运单id值
				id: 0,
				longitude: null,
				latitude: null
			}
		},
		
		methods: {
			getMessage(data) {
				this.message =data;
			},
			
			getMessage2(data) {
				this.message2 = data;
			},
			
			imgPath(data) {
				this.imgPathList = data;
			},
			
			async submitMessage() {
				const opts = {
					url: "/personal/driver/modReceiptImg",
					method: "post"
				}
				const param = {
					id: this.id,
					imgs: this.imgPathList,
					arriveLateReason: this.message,
					disburdenLateReason: this.message2
				}
				
				if(this.imgPathList.length !== 0) {
					const res = await this.$http.httpTokenRequest(opts, param);
					console.log(res);
					res.data.code === 0 &&
						uni.navigateBack({
							delta: 1
						})
				}else {
					uni.showModal({
						content: "请至少上传一张图片"
					})
				}
			},
			
			getLocation() {
				var that = this;
				uni.getLocation({
					type: 'wgs84',
					success: function(res) {
						console.log('当前位置的经度：' + res.longitude)
						that.longitude = res.longitude;
						console.log('当前位置的纬度：' + res.latitude);
						that.latitude = res.latitude;
					}
				})
			}
			
		},
		
		
		onLoad(options) {
			this.id = options.id
			this.getLocation();
		},
		
		components: {
			infoBox,
			btn,
			messBox,
			uploadMode
		}
		
		
	}
